<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Reset Password</h1>
       <div class="chartWrap">
        </div>
    </section>
    <div class="center">
        <span class="tisp">Enter the Email address or mobile number when you registered. We will send you a code to reset password.</span>
        <van-field v-model="username" placeholder="Email or mobile number" />
    </div>
    
    <van-button type="danger" @click="applyReset">Verify</van-button>
</div>
</template>

<script>
import {sendEmail,sendSms} from '@/api/msg'
export default {
  name: 'resetPwd',
  data () {
    return {
     username:""
    
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    applyReset(){
        if(this.username.indexOf('@')>-1){
            sendEmail(this.username,"forget").then(res=>{
                this.$toast('Code send successfully');
                this.$router.push({path:'verifyEmail',query:{username:this.username,type:'forget'}});
            }).catch(e=>{
                this.$toast(e.message);
            });
        }else{
            sendSms(this.username,"forget").then(res=>{
                this.$toast('Code send successfully');
                 this.$router.push({path:'verifyMobile',query:{username:this.username,type:'forget'}});
            }).catch(e=>{
                this.$toast(e.message);
            });
        }
    }


  }
}
</script>

<style lang="scss" scoped>
    .center{
        width: 100%;
        padding: 0 6%;
        border-top: 10px solid #f4f4f4;
        padding-top: 20px;
    }
    .tisp{
        font-size: 12px;
        color: #323233a3;
        
    }
    .van-button--danger{
        width: 92%;
        margin-left: 4%;
        margin-top: 102px;
    }
    .van-cell{
        padding-top: 30px;
        padding-left: 0;
        border-bottom: solid 1px #f2f3f5;
    }
</style>



